body {
  font-family: 'Roboto', 'Helvetica', 'Arial', sans-serif;
  color: #757575;
  line-height: 1.15;
  background: #FAFAFA;
}
html,
body,
#root,
#root > #page,
#root > #page > .container > .row,
#root > #page > .container > .row > div,
#root > #page > .container > .row > div > #scheduler {
  height: 100%;
}
#root > #page > .container {
  height: calc(100% - 39px);
  max-width: none;
}
#root > #page > .container > .row > div:first-child {
  padding-right: 0;
}
#root > .container > .row > div:last-child {
  padding-left: 0;
}
h1 {
  font-size: 80px;
  font-weight: 300;
  line-height: 1;
  margin-bottom: 40px;
}
h2 {
  font-size: 30px;
  font-weight: 400;
  line-height: 40px;
  margin-bottom: 15px;
}
.buttons {
  display: flex;
}
.buttons div {
  background: rgba(158, 158, 158, 0.2);
  padding: 6px 10px;
  border-radius: 2px;
  cursor: pointer;
  text-transform: uppercase;
  font-size: 14px;
}
.buttons div:first-child {
  background: #3f51b5;
  color: white;
  margin-right: 10px;
}
.buttons div:last-child {
  margin-right: 0;
}
label {
  margin-bottom: 0;
}
.separator {
  margin: 0 5px;
}
#scheduler {
  padding: 16px 30px 16px 0;
  overflow-y: auto;
  max-width: 600px;
  margin: 0 auto;
}
#scheduler #max-running-tasks {
  cursor: pointer;
}
.tasks {
  padding: 0;
  margin-bottom: 30px;
}
.tasks .task {
  list-style: none;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  background: white;
  margin-bottom: 16px;
}
.tasks .task .content {
  padding: 16px;
  padding-bottom: 12px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-bottom: none;
}
.tasks .task .header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
}
.tasks .task .header .status {
  text-align: right;
  line-height: 1;
}
.tasks .task .header .status .iterations {
  font-size: 12px;
  color: #AAA;
}
#tasks-queued.tasks .task .header {
  cursor: -webkit-grab;
  cursor: grab;
}
.tasks .task .header .project-name {
  font-weight: 300;
  font-size: 24px;
  margin-bottom: 10px;
}
.tasks .task .progress {
  background: #3f51b5;
  height: 2px;
  margin-bottom: 10px;
  border-radius: 0;
  -webkit-transition: width 400ms ease-in-out;
  transition: width 400ms ease-in-out;
}
.tasks .task .preset-name {
  font-size: 14px;
  cursor: pointer;
}
.tasks .task .try-number {
  background: rgba(158, 158, 158, 0.2);
  padding: 1px 7px 2px 7px;
  margin-right: 7px;
  display: inline-block;
  line-height: 1;
  vertical-align: top;
}
.tasks .task .toolbar {
  background: rgba(158, 158, 158, 0.2);
  padding: 5px 16px 1px 16px;
  line-height: 1;
  border: 1px solid rgba(0, 0, 0, 0.1);
}
.tasks .task .toolbar > span {
  display: flex;
  align-items: center;
}
.tasks .task .toolbar .action {
  background: #3f51b5;
  padding: 0 10px;
  text-transform: uppercase;
  display: inline-flex;
  align-items: center;
  color: white;
  border-radius: 2px;
  font-weight: 500;
  /* box-shadow: 0 2px 2px 0 rgba(0,0,0,.14),0 3px 1px -2px rgba(0,0,0,.2),0 1px 5px 0 rgba(0,0,0,.12);*/
  height: 24px;
  font-size: 13px;
  cursor: pointer;
  margin-right: 5px;
  margin-bottom: 4px;
}
.tasks .task .toolbar .action i {
  margin-right: 7px;
}
.tasks .task .toolbar .action span {
  margin-top: 2px;
}
.tasks .task .toolbar .dropdown {
  display: inline-flex;
  margin-bottom: 4px;
}
.tasks .task .toolbar .action.dropdown-toggle i {
  margin: 0;
}
.tasks .task .toolbar .current-action {
  flex: 2 1;
  text-align: right;
  font-size: 12px;
}
.tasks .task.on-drag-over {
  border: 3px dashed #3f51b5;
  box-shadow: none;
}
.tasks .task.on-drag-over .content,
.tasks .task.on-drag-over .toolbar {
  visibility: hidden;
}
#root div.jsoneditor-tree div.jsoneditor-tree-inner {
  padding-bottom: 100px;
}
.jsoneditor .inherited-value .jsoneditor-field,
.jsoneditor .inherited-value .jsoneditor-value {
  color: #888888;
}
.jsoneditor table.jsoneditor-values.inherited-value td .revert {
  display: none;
}
.jsoneditor table.jsoneditor-values:not(.inherited-value) td .revert {
  width: 24px;
  height: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  color: #3f51b5;
  cursor: pointer;
}
.jsoneditor table.jsoneditor-values.new-value td .revert i:before {
  content: "\f00d";
}
#project-manager {
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-top: 0;
  border-bottom: 0;
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
}
#project-manager #projects-toolbar {
  display: flex;
  padding: 5px 16px;
  background: #e8e8e8;
  justify-content: space-between;
  z-index: 10;
}
#project-manager #projects-toolbar #project-selector span {
  margin-right: 20px;
}
#project-manager #projects-toolbar #project-selector span i {
  color: #BBB;
}
#project-manager #projects-toolbar #project-selector span.active i {
  color: inherit;
  cursor: pointer;
}
#project-manager #projects-toolbar #project-toolbar {
  display: flex;
  flex-direction: row;
}
#project-manager #projects-toolbar #project-toolbar #code-version {
  padding: 0 10px;
  border-radius: 2px;
  font-weight: 500;
  margin-right: 10px;
  color: #757575;
  background: #f2f2f2;
  display: flex;
  align-items: stretch;
  border: 1px solid rgba(0, 0, 0, 0.1);
  cursor: pointer;
}
#project-manager #projects-toolbar #project-toolbar #tb-link {
  background: #F57C00;
  color: white;
  border-radius: 2px;
  padding: 0 3px;
  font-weight: 500;
  cursor: pointer;
  display: flex;
  align-items: center;
}
#project-manager #projects {
  overflow-y: hidden;
  flex: 1 1 auto;
  height: 100px;
  background: #FAFAFA;
  z-index: 10;
}
#project-manager #projects .project {
  height: 100%;
  display: flex;
  flex-direction: column;
}
#project-manager #projects .project .tabs {
  display: flex;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-right: none;
  border-left: none;
  background: white;
  flex: 0 0 auto;
}
#project-manager #projects .project .tabs div {
  padding: 16px;
  border-right: 1px solid rgba(0, 0, 0, 0.1);
  flex: 2 1;
  text-align: center;
  font-weight: 300;
  font-size: 24px;
  background: rgba(0, 0, 0, 0.05);
  cursor: pointer;
}
#project-manager #projects .project .tabs div:hover {
  background: rgba(0, 0, 0, 0.025);
}
#project-manager #projects .project .tabs div.tab-active,
#project-manager #projects .project .tabs div.tab-active:hover {
  background: white;
}
#project-manager #projects .project .tabs div:last-child {
  border: none;
}
#project-manager #projects .project .sorting {
  padding: 5px 16px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
#project-manager #projects .project .sorting > div > * {
  margin-right: 10px;
}
#project-manager #projects .project .sorting > div > .fa,
#project-manager #projects .project .sorting > div > .fas {
  cursor: pointer;
}
#project-manager #projects .project > .preset-filter {
  border: none;
}
#project-manager #projects .project .tab {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  min-height: 0;
}
#project-manager #projects .project .presets-tab,
#project-manager #projects .project .tasks-tab {
  overflow-y: auto;
  flex: 1 1 auto;
}
#project-manager #projects .project ul {
  padding: 0;
  margin-bottom: 0;
}
#project-manager #projects .project ul .item {
  list-style: none;
}
#project-manager #projects .project ul .item-preset[draggable="true"] .header {
  cursor: -webkit-grab;
  cursor: grab;
}
#project-manager #projects .project ul .item-preset.on-drag-over {
  border: 3px dashed #3f51b5;
  box-shadow: none;
}
#project-manager #projects .project ul .item-preset.on-drag-over .header {
  visibility: hidden;
}
#project-manager #projects .project ul .item-preset .grip-icon {
  width: 28px;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
}
#project-manager #projects .project ul .item-preset .group-header,
#project-manager #projects .project ul .item-preset .header {
  cursor: pointer;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
#project-manager #projects .project ul .item-preset .group-header {
  background: white;
  font-weight: 300;
  justify-content: center;
  font-size: 24px;
  padding: 0 0 0 16px;
  text-transform: uppercase;
}
#project-manager #projects .project ul .item-preset .header {
  height: 40px;
  padding-left: 16px;
  padding-right: 10px;
  background: rgba(0, 0, 0, 0.05);
}
#project-manager #projects .project ul .item-preset .header .title {
  font-weight: 300;
  font-size: 18px;
}
#project-manager #projects .project ul .item-preset .header .toolbar {
  display: flex;
  align-items: center;
}
#project-manager #projects .project ul .item-task,
#project-manager #projects .project ul .item-choice {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  background: white;
  display: flex;
}
#project-manager #projects .project ul .item-task.task-running {
  background-image: -webkit-repeating-linear-gradient(135deg, #f4f5ff, #f4f5ff 11px, white 10px, white 20px);
  background-image: repeating-linear-gradient(-45deg, #f4f5ff, #f4f5ff 11px, white 10px, white 20px);
  background-size: 28px 28px;
  -webkit-animation: move 2s linear infinite;
          animation: move 2s linear infinite;
}
@-webkit-keyframes move {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 28px 0;
  }
}
@keyframes move {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 28px 0;
  }
}
#project-manager #projects .project ul .item-task.item-highlight {
  -webkit-animation: highlight 1500ms ease-out;
          animation: highlight 1500ms ease-out;
}
@-webkit-keyframes highlight {
  0% {
    background-color: #4f61f4;
  }
  100 {
    background-color: white;
  }
}
@keyframes highlight {
  0% {
    background-color: #4f61f4;
  }
  100 {
    background-color: white;
  }
}
#project-manager #projects .project ul .item-choice.item-abstract {
  font-style: italic;
}
#project-manager #projects .project ul .item-task .content,
#project-manager #projects .project ul .item-choice .content {
  flex: 1 1 auto;
  padding: 16px;
}
#project-manager #projects .project ul .item-task .content .title,
#project-manager #projects .project ul .item-choice .content .title {
  font-weight: 300;
  font-size: 24px;
  margin-bottom: 10px;
}
#project-manager #projects .project ul .item-task .content .title .try-number,
#project-manager #projects .project ul .item-choice .content .title .try-number {
  background: #ececec;
  padding: 4px 10px;
  margin-right: 10px;
  display: inline-block;
  line-height: 1;
  font-size: 20px;
  vertical-align: top;
}
#project-manager #projects .project ul .item-task .content .footer,
#project-manager #projects .project ul .item-choice .content .footer {
  font-size: 14px;
  font-weight: 300;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  padding-top: 10px;
  margin-top: 10px;
}
#project-manager #projects .project ul .item-task .content .footer .task-error {
  color: #ef767a;
  font-weight: 400;
}
#project-manager #projects .project ul .item-task .content .footer span:not(.task-error):before {
  content: "-";
  margin: 0 7px;
}
#project-manager #projects .project ul .item-task .content .footer span:first-child:before,
#project-manager #projects .project ul .item-choice .content .footer span:first-child:before {
  content: "";
  margin: 0;
}
#project-manager #projects .project ul .item-task .content .footer span span,
#project-manager #projects .project ul .item-choice .content .footer span span {
  font-weight: 400;
}
#project-manager #projects .project ul .item-task .toolbar,
#project-manager #projects .project ul .item-choice .toolbar {
  border-left: 1px solid rgba(0, 0, 0, 0.1);
  padding: 10px;
  background: #f2f2f2;
  display: flex;
  flex-direction: column;
}
#project-manager #projects .project ul .item .toolbar .action {
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  width: 28px;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #3f51b5;
  border-radius: 2px;
  cursor: pointer;
  color: white;
  font-size: 12px;
}
#project-manager #projects .project ul .item .toolbar .action.action-disabled {
  cursor: not-allowed;
  background: gray;
}
#project-manager #projects .project ul .item-task .toolbar .action,
#project-manager #projects .project ul .item-choice .toolbar .action {
  margin-bottom: 4px;
}
#project-manager #projects .project ul .item-preset .header .toolbar .action {
  margin-left: 4px;
}
.tasks .task .toolbar .action.dropdown-toggle,
#project-manager #projects .project ul .item-task .toolbar .action.dropdown-toggle,
#project-manager #projects .project ul .item-choice .toolbar .action.dropdown-toggle {
  margin: 0;
}
#project-manager #projects .project ul .item-task .toolbar .action.dropdown-toggle,
#project-manager #projects .project ul .item-choice .toolbar .action.dropdown-toggle {
  height: 14px;
}
.tasks .task .toolbar .action.dropdown-toggle:after,
#project-manager #projects .project ul .item-task .toolbar .action.dropdown-toggle:after,
#project-manager #projects .project ul .item-choice .toolbar .action.dropdown-toggle:after {
  display: none;
}
.tasks .task .toolbar .dropdown-menu,
#project-manager #projects .project ul .item-task .toolbar .dropdown-menu,
#project-manager #projects .project ul .item-choice .toolbar .dropdown-menu {
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  min-width: auto;
  padding: 7px;
  border: 1px solid #E3E3E3;
  border-radius: 0;
  margin-top: 5px;
}
.tasks .task .toolbar .dropdown-menu {
  left: -42px !important;
}
#project-manager #projects .project ul .item .toolbar .dropdown-menu {
  left: 4px !important;
}
.tasks .task .toolbar .dropdown-menu:after,
#project-manager #projects .project ul .item .toolbar .dropdown-menu:after {
  content: " ";
  position: absolute;
  bottom: 100%;
  left: 50%;
  margin-left: -8px;
  border: 10px solid transparent;
  border-bottom-color: white;
}
.tasks .task .toolbar .dropdown-menu .action:last-child,
#project-manager #projects .project ul .item .toolbar .dropdown-menu .action:last-child {
  margin: 0;
}
#project-manager #projects .project .tab-toolbar {
  display: flex;
  padding: 5px 16px;
  background: rgba(158, 158, 158, 0.2);
  flex-direction: row;
  justify-content: space-between;
  flex: 0 0 auto;
}
#project-manager #projects .project .tab-toolbar label {
  margin-bottom: 0;
  display: flex;
  align-items: center;
}
#project-manager #projects .project .tab-toolbar label input {
  margin-right: 10px;
}
#project-manager #projects .project .tab-toolbar .buttons div:last-child {
  margin-right: 0;
}
.prompt-wrapper {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.5);
  z-index: 5;
}
.prompt-wrapper .prompt {
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  background: white;
  max-width: 300px;
  border-radius: 2px;
}
.prompt-wrapper .prompt.preset-prompt {
  max-width: 600px;
  width: 600px;
}
.prompt-wrapper .prompt .prompt-header {
  background: #3f51b5;
  padding: 5px 16px;
  color: white;
}
.prompt-wrapper .prompt .prompt-text {
  font-size: 14px;
  padding: 16px;
  padding-bottom: 0;
}
.prompt-wrapper .prompt > input,
.prompt-wrapper .prompt > select {
  margin: 10px 16px 0 16px;
  text-align: right;
  width: calc(100% - 32px);
}
.prompt-wrapper .jsoneditor {
  width: calc(100% - 32px);
  margin: 10px 16px 16px 16px;
  height: 250px;
  border-color: #3f51b5;
}
.prompt-wrapper .jsoneditor div.jsoneditor-menu {
  background-color: #3f51b5;
  border-color: #3f51b5;
}
.editor .buttons,
.prompt-wrapper .prompt .buttons {
  display: flex;
  justify-content: flex-end;
  margin: 16px 16px 16px 16px;
}
.editor.task-editor {
  flex: 1 1;
}
.editor .header {
  padding: 5px 16px;
  background: rgba(158, 158, 158, 0.2);
}
.editor .field {
  margin: 10px 16px;
}
.editor .preset-filter {
  margin: 30px 16px;
}
.editor .field {
  display: flex;
  align-items: center;
  justify-content: stretch;
}
.editor .field label {
  margin: 0;
  width: 150px;
}
.editor .field input,
.editor .field select {
  flex: 2 1;
  padding-left: 5px;
}
.editor .field input[type="checkbox"] {
  flex: none;
}
.editor .jsoneditor {
  width: calc(100% - 32px);
  resize: vertical;
  margin: 16px;
  height: 250px;
  border-color: #3f51b5;
}
.editor .jsoneditor-readOnly .jsoneditor {
  border-color: rgba(158, 158, 158, 0.2);
  height: auto;
}
.editor .jsoneditor-readOnly .ace-jsoneditor .ace_marker-layer .ace_active-line,
.editor .jsoneditor-readOnly .ace-jsoneditor .ace_gutter-active-line {
  background-color: inherit;
}
.editor .jsoneditor-readOnly .ace-jsoneditor .ace_scroller {
  background-color: rgba(158, 158, 158, 0.1);
}
.editor .jsoneditor-readOnly .ace_cursor-layer {
  display: none;
}
.editor .editor-loading {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 250px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  margin: 16px;
}
.editor .jsoneditor div.jsoneditor-menu {
  background-color: #3f51b5;
  border-color: #3f51b5;
}
.task-editor .command {
  cursor: pointer;
}
.slide-editor {
  position: absolute;
  width: 400px;
  left: -400px;
  top: 0;
  border-left: 1px solid rgba(0, 0, 0, 0.1);
  border-right: 1px solid rgba(0, 0, 0, 0.1);
  background: #fafafa;
  height: 100%;
  -webkit-animation: slide-in 0.4s ease;
          animation: slide-in 0.4s ease;
  overflow-y: auto;
}
@-webkit-keyframes slide-in {
  0% {
    -webkit-transform: translateX(400px);
            transform: translateX(400px);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
@keyframes slide-in {
  0% {
    -webkit-transform: translateX(400px);
            transform: translateX(400px);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
.slide-editor .header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}
.slide-editor .header i {
  padding: 2px 10px;
  margin: -2px -10px;
}
.task-viewer .title,
.task-viewer .metadata,
.task-viewer h2,
.task-viewer .notes,
.task-viewer .presets,
.task-viewer .checkpoints {
  margin: 10px 16px;
}
.task-viewer .title {
  font-weight: 300;
  font-size: 24px;
}
.task-viewer .title .try-number {
  background: rgba(158, 158, 158, 0.2);
